<template>
    <div class="bg" ref="miniProWeb">
        <div class="miniProInfo">
            <div class="name">{{title}}</div>
            <div class="desc">{{desc}}</div>
            <!-- https://cdn.youlianyc.com/image/static/19da57ec3a6893d71cc858f5dade21a6df6cefed.jpg -->
            <div v-longpress="longtap" class="img_"><img :src="qrcode"></div>
            <div class="longTapTxt">长按识别小程序</div>
        </div>
    </div>
</template>
<style lang="scss" scoped>
.bg{
    background: #35DDCA url("https://cdn.youlianyc.com/image/static/bf1a305967d0c6cc9cad6d4dc6c7e99ea007e9d6.jpg") bottom center no-repeat;
    background-size: 100%;
    overflow: hidden;
    position: relative;
    z-index: 2;
    .miniProInfo {
        border-radius: 40px;
        background: #fff;
        padding: 34px 31px;
        margin: 50px 22px;
        
        .name {
            font-size:22px;
            font-family:PingFang-SC-Medium;
            font-weight:500;
        }
        .desc {
            font-size:13px;
            font-family:PingFang-SC-Regular;
            font-weight:400;
            margin: 12px 0 76px;
        }
        .img_ {
            img {
                width: 157px;
                height: 157px;
            }
        }
        .longTapTxt {
            color: #888;
            font-size:15px;
            margin-top: 26px;
        }
        .bottom-img {
            position: absolute;
            z-index: 1;
            bottom: 0;
            left: 0;
            width: 100%;
            height: auto;
        }
    }
}

</style>
<script>
export default {
    data () {
        return {
            title: '',
            desc: '',
            qrcode: '',
            longpress: 0,
            clientHeight:'',
        }
    },
    mounted () {
        // 获取浏览器可视区域高度
        this.clientHeight =   `${document.documentElement.clientHeight}`          
        //document.body.clientWidth;
        //console.log(self.clientHeight);
        window.onresize = function temp() {
            this.clientHeight = `${document.documentElement.clientHeight}`;
        };
        let {title, desc, qrcode} = this.$route.query
        this.title = decodeURIComponent(title)
        this.desc = decodeURIComponent(desc)
        this.qrcode = decodeURIComponent(qrcode)
        document.title = '精品推荐'
    },
    watch: {
      clientHeight: function () {
        this.changeFixed(this.clientHeight)
      }
    },
    methods: {
        changeFixed(clientHeight){ //动态修改样式
            // console.log(clientHeight);
            this.$refs.miniProWeb.style.height = clientHeight+'px';
      },
    }
}
</script>


